import React, { Component } from "react";
import { DragDropContext } from "react-beautiful-dnd";
import { isEmpty, cloneDeep } from "lodash";
import ViewSource from "./ViewSource";
import ViewType from "./ViewType";
import TabHeading from "./TabHeading";
import Section from "./Section";
import ViewSettings from "./ViewSettings";
import Modal from "./Modal";
import "./index.css";

class Container extends Component {
	state = {
		env: "local",
		viewSource: {
			formId: "",
		},
		viewSettings: {
			multipleentries: {
				perPage: 25,
			},
			singleEntry: {},
			filter: {},
			sort: {
				direction: "ASC",
			},
		},
		sections: {
			beforeloop: {
				rows: [],
				tabname: "multipleentries",
				label: "Before Entry List Fields",
			},
			loop: {
				rows: [],
				tabname: "multipleentries",
				label: "Entry List Fields",
			},
			afterloop: {
				rows: [],
				tabname: "multipleentries",
				label: "After Entry List Fields",
			},
			singleloop: {
				rows: [],
				tabname: "singleEntry",
				label: "Single Entry Fields",
			},
		},
		rows: {},

		columns: {},
		fields: {},
		formsList: [],
		formFields: {},
		defaultFields: {
			pagination: {
				label: "Pagination",
				type: "pagination",
			},
			paginationInfo: {
				label: "Pagination Info",
				type: "pagination_info",
			},
		},
		viewTypes: ["table"],
		viewType: "table",
		activeTab: "multipleentries",
		activeSection: "",
		modal: {
			type: "layout",
			label: "layout",
			isVisible: false,
			currentId: null,
		},
	};

	componentDidMount() {
		const {
			view_forms,
			_view_form_fields,
			_view_saved_settings,
			_view_config,
		} = window;
		// update forms list
		if (!!view_forms) {
			let newState = {};
			const savedSettings = JSON.parse(_view_saved_settings);
			const viewFormFields = JSON.parse(_view_form_fields);
			// Add extra fields to form Fields
			const formFields = this.addLoopFields(viewFormFields);
			if (!isEmpty(savedSettings)) {
				// Update Single Entry Tabname to use "singleEntry", previoulsy it was using "singleentries" -
				// This need to removed in future version, here for backward compatibilty
				savedSettings.sections.singleloop.tabname = "singleEntry";

				newState = {
					formFields,
					viewSource: {
						formId: savedSettings.formId,
					},
					sections: savedSettings.sections,
					columns: savedSettings.columns,
					fields: savedSettings.fields,
					rows: savedSettings.rows,
					activeTab: savedSettings.activeTab,
					viewSettings: savedSettings.viewSettings,
					viewType: savedSettings.viewType,
				};
			}
			// check if there are any config variables & set them
			if (!isEmpty(_view_config)) {
				const view_config = JSON.parse(_view_config);
				Object.keys(view_config).forEach((key, index) => {
					newState[key] = view_config[key];
				});
			}

			newState.formsList = JSON.parse(view_forms);
			newState.env = "production";
			this.setState(newState);
		}
	}

	addLoopFields = (viewFormFields) => {
		viewFormFields.entryId = {
			id: "entryId",
			label: "Entry ID",
			fieldType: "entryId",
		};
		viewFormFields.sequenceNumber = {
			id: "sequenceNumber",
			label: "Sequence Number",
			fieldType: "sequenceNumber",
		};
		return viewFormFields;
	};

	updateViewSource = (e) => {
		const { confirm, _view_config } = window;
		if (this.state.viewSource.formId != "") {
			const confirmChange = confirm(
				"Are you sure want to change the View Source? This will reset all your view layout settings."
			);
			if (!confirmChange) {
				return;
			}
		}
		const view_config = JSON.parse(_view_config);
		const viewSource = cloneDeep(this.state.viewSource);
		viewSource.formId = e.target.value;
		if (this.state.env === "production") {
			// Get Form Fields for this form.
			const { ajaxurl, jQuery } = window;
			var data = {
				action: view_config.prefix + "_views_get_form_fields",
				form_id: e.target.value,
			};
			const that = this;
			jQuery.post(ajaxurl, data, function(response) {
				const currentSections = cloneDeep(that.state.sections);
				const currentRows = cloneDeep(that.state.rows);
				const currentColumns = cloneDeep(that.state.columns);
				const fields = cloneDeep(that.state.fields);
				// remove all rows from  loop sections
				//sections.loop.rows =  [] ;
				Object.keys(currentSections).forEach((key, index) => {
					currentSections[key] = {
						...currentSections[key],
						rows: [],
					};
				});
				//make list of rows empty
				Object.keys(currentRows).forEach(
					(key) => delete currentRows[key]
				);
				//make list of columns empty
				Object.keys(currentColumns).forEach(
					(key) => delete currentColumns[key]
				);
				// make list of fields empty
				Object.keys(fields).forEach((key) => delete fields[key]);
				const receivedFormFields = JSON.parse(response);
				const viewFormFields = { ...receivedFormFields };

				const grid = that.createNewRow(
					currentSections,
					currentRows,
					currentColumns,
					"loop"
				);
				const { sections, rows, columns } = grid;
				// Add extra fields to form Fields
				const formFields = that.addLoopFields(
					viewFormFields,
					view_config
				);

				that.setState({
					formFields,
					sections,
					rows,
					columns,
					fields,
					viewSource,
				});
			});
		} else {
			this.setState({
				viewSource,
			});
		}
	};

	updateViewType = (e) => {
		const { confirm } = window;
		const confirmChange = confirm(
			"Are you sure want to change the View Type? This will reset all your View layout settings."
		);
		if (!confirmChange) {
			return;
		}
		if (e.target.value == "table") {
			const currentSections = cloneDeep(this.state.sections);
			const currentRows = cloneDeep(this.state.rows);
			const currentColumns = cloneDeep(this.state.columns);
			const fields = cloneDeep(this.state.fields);

			const sectionRows = currentSections.loop.rows;

			sectionRows.forEach((rowId, index) => {
				// Remove the colums from columns list which were present in the row to be deleted
				currentRows[rowId]["cols"].forEach((columnId) => {
					currentColumns[columnId]["fields"].forEach((fieldId) => {
						delete fields[fieldId];
					});

					delete currentColumns[columnId];
				});

				// Remove the selected row from the rows object
				delete currentRows[rowId];
			});
			currentSections.loop.rows = [];

			const grid = this.createNewRow(
				currentSections,
				currentRows,
				currentColumns,
				"loop"
			);
			const { sections, rows, columns } = grid;

			this.setState({
				sections,
				rows,
				columns,
				fields,
				viewType: e.target.value,
			});
		} else {
			this.setState({
				viewType: e.target.value,
			});
		}
	};

	addRow = (event, sectionName) => {
		const currentSections = cloneDeep(this.state.sections);
		const currentRows = cloneDeep(this.state.rows);
		const currentColumns = cloneDeep(this.state.columns);

		const grid = this.createNewRow(
			currentSections,
			currentRows,
			currentColumns,
			sectionName
		);
		const { sections, rows, columns, newRowId } = grid;
		// Also open chose layout model
		const modal = cloneDeep(this.state.modal);
		modal.isVisible = true;
		modal.type = "layout";
		modal.label = "Choose Layout";
		modal.currentId = newRowId;

		this.setState({
			sections,
			rows,
			columns,
			modal,
			activeSection: sectionName,
		});
	};

	createNewRow = (sections, rows, columns, sectionName) => {
		const newRowId = this.generateRandomId("row");
		const newColumnId = this.generateRandomId("column");
		sections[sectionName].rows.push(newRowId);
		// Add new row id to rows list
		rows[newRowId] = { cols: [newColumnId] };
		// Add new dummy column id to columns list

		columns[newColumnId] = {
			title: "Column",
			fields: [],
			size: "1",
		};

		const grid = {
			sections,
			rows,
			columns,
			newRowId: newRowId,
		};
		return grid;
	};

	deleteRow = (event, rowId, sectionName) => {
		const sections = { ...this.state.sections };
		const rows = { ...this.state.rows };
		const columns = { ...this.state.columns };
		const currentRowObj = { ...rows[rowId] };
		let columnContainFields = false;

		currentRowObj["cols"].forEach((columnId, index) => {
			const columnFields = columns[columnId].fields;
			if (
				typeof columnFields !== "undefined" &&
				columnFields.length > 0
			) {
				// the array is defined and not empty then return by showing an alert
				columnContainFields = true;
			}
		});

		if (columnContainFields) {
			alert("Can't delete this row as it contains fields");
			//return;
		}
		// Remove the colums from columns list which were present in the row to be deleted
		currentRowObj["cols"].forEach((columnId, index) => {
			delete columns[columnId];
		});
		// Remove the selected row from the rows object
		delete rows[rowId];

		//Remove the row Id from the Sections rows list
		const sectionItemRows = [...sections[sectionName].rows];
		sectionItemRows.splice(sectionItemRows.indexOf(rowId), 1);

		sections[sectionName] = {
			...sections[sectionName],
			rows: sectionItemRows,
		};

		this.setState({
			sections,
			rows,
			columns,
		});
	};

	onDragEnd = (result) => {
		const { destination, source, draggableId, type } = result;

		if (!destination) {
			return;
		}

		if (
			source.droppableId === destination.droppableId &&
			source.index === destination.index
		) {
			return;
		}

		// check if we are moving rows
		if (type.includes("section")) {
			this.reorderRows(destination, source, draggableId, type);
		} else {
			this.reorderColumns(destination, source, draggableId, type);
		}
	};

	reorderRows = (destination, source, draggableId, type) => {
		const sections = cloneDeep(this.state.sections);
		const sectionRows = sections[source.droppableId].rows;

		sectionRows.splice(source.index, 1);
		sectionRows.splice(destination.index, 0, draggableId);
		sections[source.droppableId].rows = sectionRows;

		this.setState({
			sections,
		});
	};

	reorderColumns = (destination, source, draggableId, type) => {
		let updatedDestinationFields = "";
		let updatedSourceFields = "";

		if (source.droppableId === destination.droppableId) {
			const columnFields = [
				...this.state.columns[source.droppableId].fields,
			];
			columnFields.splice(source.index, 1);
			columnFields.splice(destination.index, 0, draggableId);
			updatedDestinationFields = columnFields;
			updatedSourceFields = columnFields;
		} else {
			updatedSourceFields = [
				...this.state.columns[source.droppableId].fields,
			];
			updatedSourceFields.splice(source.index, 1);

			updatedDestinationFields = [
				...this.state.columns[destination.droppableId].fields,
			];
			updatedDestinationFields.splice(destination.index, 0, draggableId);
		}

		const columns = {
			...this.state.columns,
			[source.droppableId]: {
				...this.state.columns[source.droppableId],
				fields: updatedSourceFields,
			},
			[destination.droppableId]: {
				...this.state.columns[destination.droppableId],
				fields: updatedDestinationFields,
			},
		};

		// columns[ source.droppableId ].fields.splice( source.index, 1 );
		// columns[ destination.droppableId ].fields.splice( destination.index, 0, draggableId );

		this.setState({
			columns,
		});
	};

	updateColumnLayout = (event, layout) => {
		layout = layout.split(",");
		const modal = { ...this.state.modal };
		const columns = { ...this.state.columns };
		const currentRowCols = [...this.state.rows[modal.currentId].cols];
		const currentRowLength = currentRowCols.length;
		// Check more columns are added in the row
		const columnLengthChanged = layout.length - currentRowLength;

		// New columns are added
		if (columnLengthChanged > 0) {
			for (let i = 0; i < columnLengthChanged; i++) {
				const newColumnId = this.generateRandomId("column");
				currentRowCols.push(newColumnId);
				columns[newColumnId] = {
					title: "Column",
					fields: [],
					size: "",
				};
			}
		}

		// Column numbers are reduced
		if (columnLengthChanged < 0) {
			for (let i = layout.length; i < currentRowLength; i++) {
				const currentColumn = currentRowCols[i];
				const previousColumn = currentRowCols[layout.length - 1];
				columns[previousColumn] = {
					...columns[previousColumn],
					fields: [
						...columns[previousColumn].fields,
						...columns[currentColumn].fields,
					],
				};
				//columns[ previousColumn ].fields = [ ...columns[ previousColumn ].fields, ...columns[ currentColumn ].fields ];
			}
			currentRowCols.splice(layout.length, Math.abs(columnLengthChanged));
		}

		currentRowCols.forEach((columnId, index) => {
			columns[columnId] = {
				...columns[columnId],
				size: layout[index],
			};
			//columns[ columnId ].size = layout[ index ];
		});

		//rows[ this.state.modal.currentId ].cols = currentRowCols;
		const rows = {
			...this.state.rows,
			[modal.currentId]: {
				...this.state.rows[modal.currentId],
				cols: currentRowCols,
			},
		};
		modal.isVisible = false;
		this.setState({
			rows,
			columns,
			modal,
		});
	};

	//addField
	addField = (event, formFieldId) => {
		const modal = { ...this.state.modal };
		const fields = { ...this.state.fields };
		const formFields = { ...this.state.formFields };

		// current column id to which field should be added
		const currentColumnId = modal.currentId;

		// generate a random field id to save
		const newFieldId = this.generateRandomId("field");

		// new field id to column list of fields
		const columns = {
			...this.state.columns,
			[currentColumnId]: {
				...this.state.columns[currentColumnId],
				fields: [
					...this.state.columns[currentColumnId].fields,
					newFieldId,
				],
			},
		};
		let fieldLabel = "";
		if (
			this.state.activeSection == "beforeloop" ||
			this.state.activeSection == "afterloop"
		) {
			fieldLabel = this.state.defaultFields[formFieldId].label;
		} else {
			fieldLabel = formFields[formFieldId].label;
		}

		// Add new field to list of fields
		fields[newFieldId] = {
			formFieldId: formFieldId,
			label: fieldLabel,
			fieldSettings: {
				useCustomLabel: false,
				label: fieldLabel,
				customClass: "",
			},
		};
		// Close the modal
		modal.isVisible = false;

		// Set the new State
		this.setState({
			fields,
			columns,
			modal,
		});
	};

	// Remove Field
	removeField = (event, fieldId, colId) => {
		const columns = cloneDeep(this.state.columns);
		const fields = cloneDeep(this.state.fields);

		// Remove the fieldId object from fields list
		delete fields[fieldId];

		// Remove field from Coloumn fields list
		const newColumnFields = cloneDeep(this.state.columns[colId].fields);
		newColumnFields.splice(newColumnFields.indexOf(fieldId), 1);
		// const columns = {
		// 	...this.state.columns,
		// 	[ colId ]: {
		// 		...this.state.columns[ colId ],
		// 		fields: newColumnFields
		// 	}
		// }
		columns[colId].fields.splice(columns[colId].fields.indexOf(fieldId), 1);

		// Set the new State
		this.setState({
			fields,
			columns,
		});
	};

	saveFieldSettings = (e, fieldId, fieldSettings) => {
		const modal = { ...this.state.modal };
		const fields = cloneDeep(this.state.fields);
		fields[fieldId]["fieldSettings"] = fieldSettings;

		// reset to default modal type
		modal.type = "layout";
		modal.isVisible = false;

		// Set the new State
		this.setState({
			fields,
			modal,
		});
	};

	saveViewSetting = (e, settingType) => {
		const viewSettings = cloneDeep(this.state.viewSettings);

		viewSettings[settingType][e.target.name] = e.target.value;
		// Set the new State
		this.setState({
			viewSettings,
		});
	};
	/**
	 * Opens modal
	 */

	openModal = (event, type, label, id, sectionName) => {
		const modal = { ...this.state.modal };
		modal.isVisible = true;
		modal.type = type;
		modal.label = label;
		modal.currentId = id;
		this.setState({
			modal,
			activeSection: sectionName,
		});
	};

	closeModal = () => {
		const modal = { ...this.state.modal };
		// reset to default modal type
		modal.type = "layout";
		modal.isVisible = false;
		this.setState({
			modal,
		});
	};

	tabClickHandler = (e) => {
		this.setState({
			activeTab: e.target.id,
		});
	};
	generateRandomId = (prefix) => {
		const min = 100,
			max = 99999;
		const rand = Math.floor(Math.random() * (max - min) + min);
		const newId = prefix + rand;
		return newId;
	};

	render() {
		console.log(this.state);

		const finaleViewSettings = JSON.stringify({
			formId: this.state.viewSource.formId,
			sections: this.state.sections,
			rows: this.state.rows,
			columns: this.state.columns,
			fields: this.state.fields,
			activeTab: this.state.activeTab,
			viewSettings: this.state.viewSettings,
			viewType: this.state.viewType,
		});

		const modal = this.state.modal;
		// Modal Props
		let modalProps = {
			type: modal.type,
			label: modal.label,
			isVisible: modal.isVisible,
			currentId: modal.currentId,
			activeSection: this.state.activeSection,
			closeModal: this.closeModal,
		};

		switch (modal.type) {
			case "addField":
				modalProps = {
					...modalProps,
					formFields:
						this.state.activeSection == "beforeloop" ||
						this.state.activeSection == "afterloop"
							? this.state.defaultFields
							: this.state.formFields,
					addField: this.addField,
				};

				break;
			case "fieldSettings":
				modalProps = {
					...modalProps,
					fieldSettings: cloneDeep(
						this.state.fields[modal.currentId].fieldSettings
					),
					updateFieldSettings: this.updateFieldSettings,
					saveFieldSettings: this.saveFieldSettings,
				};

				break;
			default:
				modalProps = {
					...modalProps,
					updateColumnLayout: this.updateColumnLayout,
				};

				break;
		}

		// Field Props
		const fieldProps = {
			removeField: this.removeField,
		};

		// View Settings Props
		const viewSettingsProps = {
			viewSettings: this.state.viewSettings,
			activeTab: this.state.activeTab,
			saveViewSetting: this.saveViewSetting,
		};

		// View Source Component
		const formsList = [...this.state.formsList];
		const viewSource = formsList.map((form, index) => {
			return (
				<ViewSource
					key={`form-${form.id}`}
					formId={form.id}
					label={form.label}
				/>
			);
		});

		// Sections Component
		const sectionsObj = { ...this.state.sections };
		const activeTab = this.state.activeTab;

		const sections = Object.keys(sectionsObj)
			.map((key, index) => {
				if (activeTab === sectionsObj[key].tabname) {
					return (
						<Section
							sectionLabel={sectionsObj[key].label}
							sectionName={key}
							sectionTabName={sectionsObj[key].tabname}
							sectionRows={sectionsObj[key].rows}
							key={`section${index}`}
							id={index}
							allRows={this.state.rows}
							allColumns={this.state.columns}
							formFields={this.state.formFields}
							allFields={this.state.fields}
							viewType={this.state.viewType}
							addFieldModal={this.AddFieldModal}
							fieldProps={fieldProps}
							addRow={this.addRow}
							openModal={this.openModal}
							deleteRow={this.deleteRow}
						/>
					);
				} else {
					return null;
				}
			})
			.filter((value) => value);

		return (
			<div>
				<textarea
					className="final-view-settings"
					readOnly="readonly"
					type="text"
					value={finaleViewSettings}
					name="final_view_settings"
				/>

				{/* View Source */}
				<div className="viewSourceCont pure-form viewSectionContainer">
					<fieldset>
						<legend>Select View Source</legend>
						<select
							value={this.state.viewSource.formId}
							onChange={this.updateViewSource}
							className="view-child"
						>
							{viewSource}
						</select>
					</fieldset>
				</div>

				{/* View Type */}
				<ViewType
					onchange={this.updateViewType}
					viewType={this.state.viewType}
					viewTypes={this.state.viewTypes}
				/>

				{/* Tab Headings  */}
				<div className="viewSecMainCont">
					<ul className="viewTabHeadings pure-menu-list pure-menu-horizontal">
						<TabHeading
							tabName="multipleentries"
							tabClickHandler={this.tabClickHandler}
							label="Multiple Entries"
							activetab={this.state.activeTab}
						/>
						<TabHeading
							tabName="singleEntry"
							tabClickHandler={this.tabClickHandler}
							label="Single Entry"
							activetab={this.state.activeTab}
						/>
					</ul>

					{/* Tab Content */}
					<DragDropContext onDragEnd={this.onDragEnd}>
						{sections}
					</DragDropContext>
				</div>
				{/* View Settings */}
				<ViewSettings
					openModal={this.openModal}
					viewSettingsProps={viewSettingsProps}
				/>

				{/* Modal */}
				<Modal modalProps={modalProps} />
			</div>
		);
	}
}
export default Container;
